/* Tooltip */
a.tooltip-container,
.tooltip-container {
  border-bottom: 1px dotted;
  border-bottom-color: color;
  cursor: help;
  font-weight: 600; }

a .tooltip-content,
.tooltip-content {
  background: #152732 none repeat scroll 0 0;
  color: #fff !important;
  border-radius: 3px;
  display: none;
  font-size: 0.8rem;
  font-weight: normal;
  line-height: 1.3rem;
  margin-top: -1.25rem;
  max-width: 300px;
  padding: 0.625rem;
  position: absolute;
  z-index: 10; }

.tooltip-content::after {
  border-left: 9px solid transparent;
  border-right: 9px solid transparent;
  border-top: 7px solid #152732;
  bottom: -5px;
  clear: both;
  content: "";
  height: 0;
  left: 50%;
  margin-left: -5px;
  position: absolute;
  width: 0; }

a.tooltip-content.tooltip-bottom,
.tooltip-content.tooltip-bottom {
  margin-top: 1.25rem; }

.tooltip-content.tooltip-bottom::after {
  border-left: 9px solid transparent;
  border-right: 9px solid transparent;
  border-top: 0;
  border-bottom: 7px solid #152732;
  top: -5px; }

/* Buttons */
.button,
a.button,
a.button:link,
a.button:active,
a.button:visited {
  background: #777 none repeat scroll 0 0;
  border: 0;
  color: #fff;
  cursor: pointer;
  display: inline-block;
  font-size: 0.85rem;
  padding: 0.825rem 1rem;
  text-align: center;
  transition: all 0.20s linear 0s;
  -o-transition: all 0.20s linear 0s;
  -ms-transition: all 0.20s linear 0s;
  -moz-transition: all 0.20s linear 0s;
  -webkit-transition: all 0.20s linear 0s; }

.button.rounded-btn {
  border-radius: 4px; }

.button.rounded-full-btn {
  border-radius: 100px; }

.button:hover {
  box-shadow: 0 0 10px 100px rgba(255, 255, 255, 0.15) inset; }

.button.secondary-btn,
a.button.secondary-btn,
a.button.secondary-btn:link,
a.button.secondary-btn:active,
a.button.secondary-btn:visited {
  background: #444 none repeat scroll 0 0; }

.button.cancel-btn,
a.button.cancel-btn,
a.button.cancel-btn:link,
a.button.cancel-btn:active,
a.button.cancel-btn:visited {
  background: #dc003a none repeat scroll 0 0; }

.button.submit-btn,
a.button.submit-btn,
a.button.submit-btn:link,
a.button.submit-btn:active,
a.button.submit-btn:visited {
  background: #b4bf04 none repeat scroll 0 0; }

.button.reload-btn,
a.button.reload-btn,
a.button.reload-btn:link,
a.button.reload-btn:active,
a.button.reload-btn:visited {
  background: #ff9800 none repeat scroll 0 0; }

.button.disabled-btn {
  cursor: not-allowed !important;
  opacity: 0.2; }

.button i {
  background: rgba(0, 0, 0, 0.1) none repeat scroll 0 0;
  border-radius: 27px;
  color: #fff !important;
  display: inline-block;
  font-size: 0.8rem;
  height: 27px;
  line-height: 27px;
  margin-right: 5px;
  width: 27px;
  transition: all 0.20s linear 0s;
  -o-transition: all 0.20s linear 0s;
  -ms-transition: all 0.20s linear 0s;
  -moz-transition: all 0.20s linear 0s;
  -webkit-transition: all 0.20s linear 0s; }

.button:hover > i {
  background: rgba(0, 0, 0, 0.06) none repeat scroll 0 0; }

:root {
  --min-post-height:300px; }

body {
  background: none repeat scroll 0 0 #0b192f;
  color: #fff;
  font-family: roboto;
  font-weight: 300;
  padding-top: 95px; }

header {
  position: fixed;
  z-index: 100;
  top: 0px;
  width: 100%;
  transition: transform 200ms;
  box-shadow: 0 2px 3px #000; }

header.cache {
  transform: translateY(-100px);
  transition: transform 200ms; }

nav {
  background: none repeat scroll 0 0 #162b4d;
  position: relative; }

a.logo {
  color: #fff;
  display: inline-block;
  font-size: 2.3rem;
  font-weight: 700;
  letter-spacing: -1px;
  padding: 0.55em;
  padding: 0.55em; }

a.logo span {
  color: #FFF;
  font-weight: 100; }

header h1 {
  color: #637693;
  display: inline;
  font-size: 1rem;
  font-weight: 300;
  margin: 0;
  position: relative;
  top: -0.35rem; }

.top-nav {
  display: flex;
  flex-direction: column;
  height: 95px; }

.top-nav .barre {
  display: flex;
  flex-direction: row;
  z-index: 200;
  background-color: #162b4d; }

.top-nav .menu-mobile {
  color: #fff;
  display: block;
  font-size: 1.2rem;
  line-height: 4;
  margin-right: 1.625rem;
  max-width: 100%;
  text-align: center;
  vertical-align: middle; }

.top-nav .menu-mobile:after {
  content: "\f008";
  font-family: "mfg";
  font-size: 1.1rem;
  margin-left: 0.5rem;
  text-align: right; }

.top-nav {
  display: flex; }

.top-nav ul {
  display: flex;
  padding: 0;
  flex-direction: column;
  transform-origin: top;
  transform: translate(100vw, -100vh);
  transition: transform 200ms;
  position: fixed;
  top: 80px;
  z-index: 100;
  width: 100vw;
  height: 100vh; }

.top-nav.ouvert ul {
  transform: scaleY(1);
  transition: transform 200ms; }

.top-nav li {
  list-style: none outside none;
  cursor: pointer; }

.top-nav li a {
  color: #fff;
  font-size: 1rem;
  padding: 1.25rem; }

.top-nav li a:hover {
  background: #2b4c61; }

.top-nav li a {
  font-size: 1.1rem;
  padding: 2rem 1.25rem; }

.top-nav li a:hover {
  background: none repeat scroll 0 0 #283D5F; }

.top-nav .active-item a,
.top-nav .active-item a:hover {
  background: none repeat scroll 0 0 #0b192f; }

.social {
  padding: .6rem;
  background-color: #162b4d;
  border-top: 1px solid #0b192f;
  display: flex;
  justify-content: space-evenly; }

.social i {
  color: #637693;
  transition: color 0.20s linear 0s;
  -o-transition: color 0.20s linear 0s;
  -ms-transition: color 0.20s linear 0s;
  -moz-transition: color 0.20s linear 0s;
  -webkit-transition: color 0.20s linear 0s; }

.social i:hover {
  color: #fff; }

.top-nav li.social a {
  border: 0px;
  padding: .5rem; }

section p {
  line-height: 1.6em; }

.post-image, .post-image a {
  height: 100%;
  width: 100%;
  display: block; }

.post-image img {
  margin: 0;
  opacity: .25;
  transition: opacity 0.20s linear 0s;
  -o-transition: opacity 0.20s linear 0s;
  -ms-transition: opacity 0.20s linear 0s;
  -moz-transition: opacity 0.20s linear 0s;
  -webkit-transition: opacity 0.20s linear 0s;
  object-fit: cover; }

.accueil .post {
  position: relative;
  min-height: 300px;
  height: var(--min-post-height);
  transition: background 200ms;
  overflow: hidden; }

.accueil .post .post-image,
.accueil .post .post-text {
  position: absolute; }

.accueil .post:nth-child(5n+1) {
  background: none repeat scroll 0 0 #B71757; }

.accueil .post:nth-child(5n+2) {
  background: none repeat scroll 0 0 #0D7E71; }

.accueil .post:nth-child(5n+3) {
  background: none repeat scroll 0 0 #b1660e; }

.accueil .post:nth-child(5n+4) {
  background: none repeat scroll 0 0 #d12767; }

.accueil .post:nth-child(5n+5) {
  background: none repeat scroll 0 0 #2d95bb; }

.accueil .post:nth-child(5n+1):hover {
  background: rgba(183, 23, 87, 0); }

.accueil .post:nth-child(5n+2):hover {
  background: rgba(13, 126, 113, 0); }

.accueil .post:nth-child(5n+3):hover {
  background: rgba(177, 102, 14, 0); }

.accueil .post:nth-child(5n+4):hover {
  background: rgba(209, 39, 103, 0); }

.accueil .post:nth-child(5n+5):hover {
  background: rgba(45, 149, 187, 0); }

.accueil .post-image img:hover,
#article-section.archive .post-image img:hover {
  opacity: 0.5; }

.accueil .post-text {
  color: #fff;
  padding: 2.25rem; }

.accueil .post-text h2 {
  color: #fff;
  font-size: 2.9rem;
  font-weight: 100;
  line-height: 1em;
  margin: 0 0 1.25rem;
  opacity: 1;
  padding: 0;
  transition: opacity 0.20s linear 0s;
  -o-transition: opacity 0.20s linear 0s;
  -ms-transition: opacity 0.20s linear 0s;
  -moz-transition: opacity 0.20s linear 0s;
  -webkit-transition: opacity 0.20s linear 0s; }

.accueil .post-text h2:hover {
  opacity: 0.6; }

.accueil .post-text p {
  /*font-size: 0.9rem;
opacity: 0.6;*/ }

.post-date .date {
  font-size: 3rem; }

.post-date .month {
  font-size: 1.6rem;
  margin-bottom: 1rem;
  margin-top: -0.7rem; }

.post-date {
  font-weight: 100;
  /*opacity: 0.3;*/
  text-align: center; }

.accueil aside {
  background-color: #FFFFFF;
  text-align: center;
  padding-top: 16px; }

.accueil .meilleurs_choix {
  width: 100%;
  background-color: #0d7e71; }

.accueil .meilleurs_choix h3 {
  color: #fff;
  font-weight: 700; }

.accueil .meilleurs_choix .liste {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: flex-start; }

.accueil .meilleurs_choix .liste p {
  color: #fff; }

.accueil .meilleurs_choix .liste article {
  width: 45vw;
  margin-bottom: 18px; }

.accueil .meilleurs_choix .miniature img {
  filter: grayscale(1);
  transition: filter 200ms; }

.accueil .meilleurs_choix .miniature:hover img {
  filter: none;
  transition: filter 200ms; }

.right-align .post-image {
  float: right; }

.right-align .post-text {
  float: right;
  text-align: right; }

.right-align .post-date {
  float: right;
  text-align: right; }

.aside-block {
  background: none repeat scroll 0 0 #fff;
  margin-bottom: 1.2r5em;
  padding: 2rem; }

aside img {
  display: block;
  margin: 0; }

aside h3 {
  color: #0b192f;
  font-size: 1.6rem;
  font-weight: 300;
  line-height: 1.1;
  margin: 0 0 0.625rem; }

aside p {
  color: #637693;
  font-size: 0.85rem; }

.advertising {
  background: none repeat scroll 0 0 #fff;
  text-align: center; }

footer {
  /*background: none repeat scroll 0 0 #162b4d;*/
  display: inline-block;
  margin-bottom: 0;
  margin-top: 1.25rem;
  padding: 1.7rem;
  position: relative;
  width: 100%; }

footer p,
footer a,
footer a:link,
footer a:active,
