  color: #637693;
  font-family: georgia;
  font-size: 1.1rem;
  font-style: italic;
  font-weight: normal;
  line-height: 1.4rem;
  margin-left: 0.625rem;
  padding-left: 0.625rem; }

#article-section blockquote {
  border-left: 2px solid #637693;
  border-right: 0;
  border-top: 0;
  border-bottom: 0;
  color: #637693;
  font-family: georgia;
  font-size: 1.1rem;
  font-style: italic;
  font-weight: normal;
  line-height: 1.4;
  margin-left: 0.625rem;
  padding: 0 0 0 1.25rem; }

#article-section .post-text img {
