  display: block;
  float: left;
  font-size: 0.8rem;
  height: 30px;
  line-height: 30px;
  margin-right: 0.625rem;
  position: relative;
  text-align: center;
  top: 4px;
  width: 30px; }

address p {
  line-height: 2.5rem; }

a.logo {
  display: block;
  text-align: center; }

head h1 {
  display: block;
  text-align: center; }

header h1 {
  display: block;
  text-align: center;
  top: -1.5rem; }

.top-nav li a {
  background: none repeat scroll 0 0 #162b4d;
  border-top: 1px solid #0b192f;
  color: #fff;
  display: block; }

.social.right {
  float: none;
  text-align: center; }

#article-section .post-text {
  padding: 1rem 2rem 2rem; }

.right-align .post-text {
  text-align: left; }

.right-align .post-date {
  text-align: center; }

footer a.right {
  float: none;
  text-align: center; }

footer {
  text-align: center; }

/* ipad */
@media screen and (min-width: 768px) and (max-width: 1023px) {
  .accueil {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap; }
  .accueil .post {
    width: 50%; }
  .accueil .post .full-img {
    height: 100%; }
  .accueil .post:nth-child(5n+3) {
    width: 100%;
    overflow: hidden; }
  .accueil .post:last-of-type {
    width: 100%; } }

@media screen and (min-width: 768px) {
  .top-nav {
    flex-direction: row; }
  .top-nav .menu-mobile {
    display: none; }
  .top-nav ul {
    display: flex;
    padding: 0;
    flex-direction: row;
    transform: none;
    position: static;
    top: 80px;
    z-index: 100;
    width: auto;
    height: auto; } }

@media screen and (min-width: 1024px) {
  .accueil {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-evenly; }
  .accueil .post {
    width: 33.333%;
    overflow: hidden;
    transition: background 200ms; }
  .accueil .post .full-img {
    width: 50vw; }
  .accueil .post:last-of-type {
    width: 100%; }
  .accueil .meilleurs_choix .liste article {
    width: 20vw; } }
