.uneBiere{
    position: relative;
    padding: 5px 5px 5px 5px;
    text-align: center;
    .image{
        position: relative;
        top:0px;
        left:0px;
        opacity: 0.5;
    }
    .texte{
        position: absolute;
        width: 100%;
        top:25%;
        display: flex;
        flex-direction: column;
        font-size: 1.2em;

        .nom, .brasserie{
            display: block;
        }
    }
}